.overlayer{
    width: 100%;
    height: 100vh;
    position: absolute;
    background: url(/static/media/background.90fae1d1.jpg) center/cover;
    overflow: hidden;
    animation: hue 9s infinite alternate;
}

.overlayer::after{
    content: '';
    width: 100%;
    height: 100%;
    background: inherit;
    position: absolute;
    top: 0;
    left: 0;
    filter: blur(2rem);
    z-index: -1;
    transform: scale(1.15);
}

@keyframes hue {
    from{
        filter: hue-rotate(0);
    }
    to{
        filter: hue-rotate(360deg);
    }
}




